  <div id="app">
    <div class="top-bg"></div>
    <div class="flex-cloum main">
      <span class="title margin-center">申请企业账号认证</span>
      <div class="flex-cloum content-box">
        <el-form :model="form" ref="ruleForm" label-width="148px" :show-message="false">
          <el-form-item label="企业名称" prop="company_name" :rules="[{required: true}]">
            <div class="flex-cloum">
              <el-input v-model="form.company_name" placeholder="请输入企业名称"></el-input>
              <span class="companyNameTips">只支持中国大陆登录注册的组织，认证成功后主体全称不可更改。每个企业只能认证1个账号</span>
            </div>
          </el-form-item>

          <el-form-item label="社会信用代码" prop="company_code" :rules="[{required: true}]">
            <el-input
              type="text"
              maxlength="18"
              show-word-limit
              v-model="form.company_code"
              placeholder="请输入18位统一社会信用代码"
            ></el-input>
          </el-form-item>

          <el-form-item label="企业营业执照">
            <el-upload
              action
              :show-file-list="false"
              :before-upload="beforeFileUpload"
              :http-request="uploadLicense"
            >
              <img
                style="object-fit:cover"
                v-if="imageUrls.license_img_url"
                class="businessLicenseImage"
                :src="imageUrls.license_img_url"
              />
              <div v-else class="businessLicense-div flex-center">
                <span class="span1">上传企业营业执照</span>
                <span class="span2">支持jpg、png格式图片，分辨率72像 素/英寸，不大于3MB</span>
              </div>
            </el-upload>
          </el-form-item>

          <el-form-item label="对公账号" prop="bank_card" :rules="[{required: true}]">
            <el-input v-model="form.bank_card" @change="cardNoInput" placeholder="请输入银行卡号"></el-input>
          </el-form-item>

          <el-form-item label="开户行" prop="bank_name" :rules="[{required: true}]">
            <el-input v-model="form.bank_name" placeholder="请输入开户行"></el-input>
          </el-form-item>

          <el-form-item label="开户支行 " prop="bank_name_sub" :rules="[{required: true}]">
            <el-input v-model="form.bank_name_sub" placeholder="请输入开户行"></el-input>
          </el-form-item>

          <el-form-item label="开户行地区 " prop="bank_location" :rules="[{required: true}]">
            <el-input v-model="form.bank_location" placeholder="请输入开户行地区"></el-input>
          </el-form-item>

          <el-form-item label="企业联系人" prop="contact_name" :rules="[{required: true}]">
            <el-input v-model="form.contact_name" placeholder="请输入您的真实姓名"></el-input>
          </el-form-item>

          <el-form-item label="法人身份证">
            <div class="flex-cloum identity-div">
              <div class="flex-row">
                <el-upload
                  style="margin-right:28px"
                  action
                  :show-file-list="false"
                  :http-request="uploadCare1"
                >
                  <img
                    v-if="imageUrls.id_card_front_img_url"
                    class="cardImage"
                    :src="imageUrls.id_card_front_img_url"
                  />
                  <div v-else class="photo flex-center">
                    <img src="@/assets/img/tupian@3x.png" />
                    <span class="span1">点击上传身份证人像面</span>
                  </div>
                </el-upload>

                <el-upload action :show-file-list="false" :http-request="uploadCare2">
                  <img
                    v-if="imageUrls.id_card_reverse_img_url"
                    class="cardImage"
                    :src="imageUrls.id_card_reverse_img_url"
                  />
                  <div v-else class="photo flex-center">
                    <img src="@/assets/img/tupian@3x.png" />
                    <span class="span1">点击上传身份证国徽面</span>
                  </div>
                </el-upload>
              </div>
              <span class="span2">如果属于分支机构刚填写执照上的是准确的负责人，是否一律是法定代表人</span>
            </div>
          </el-form-item>

          <el-form-item label="联系人电话" prop="contact_phone" :rules="[{required: true}]">
            <el-input v-model="form.contact_phone" placeholder="请输入联系人电话"></el-input>
          </el-form-item>

          <el-form-item label="邮箱" prop="contact_email" :rules="[{required: true}]">
            <el-input v-model="form.contact_email" placeholder="请输入常用邮箱"></el-input>
          </el-form-item>

          <el-form-item label="公司地址" prop="contact_address" :rules="[{required: true}]">
            <el-input v-model="form.contact_address" placeholder="请输入公司地址"></el-input>
          </el-form-item>
        </el-form>
      </div>

      <div class="margin-center">
        <el-button class="submitBtn" @click="submitAction">提交</el-button>
      </div>
    </div>
  </div>
