<script>
import qiniuApi from "utils/qiniu.js";
import bankApi from "network/api/bank.js";
import companyApi from "network/api/company.js";

import { createNamespacedHelpers } from "vuex";
const { mapState, mapMutations } = createNamespacedHelpers("user");

let qiniuUrl = "http://qbbvgx8aj.bkt.clouddn.com/";

export default {
  data() {
    return {
      company_id: "", // 公司id
      form: {
        company_name: "",
        company_code: "", //社会信用代码
        license_img: "", // 企业营业执照
        bank_card: "", // 银行卡号
        bank_name: "", // 开户行
        bank_name_sub: "", // 开户支行
        bank_location: "", // 开户行地区
        contact_name: "", // 企业联系人
        id_card_front_img: "",
        id_card_reverse_img: "",
        contact_phone: "",
        contact_email: "",
        contact_address: ""
      },

      imageUrls: {
        license_img_url: "",
        id_card_front_img_url: "",
        id_card_reverse_img_url: ""
      },

      imageRes: {
        license_img_res: null,
        id_card_front_img_res: null,
        id_card_reverse_img_res: null
      }
    };
  },
  computed: {
    ...mapState(["verifystatus"])
  },

  //生命周期 - 创建完成（访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（访问DOM元素）
  mounted() {
    this.requestVerifyData();
  },
  //方法
  methods: {
    ...mapMutations(["updateVerifyStatus"]),

    requestVerifyData() {
      companyApi
        .getCompanyInfo()
        .then(res => {
          if (res.data && res.data != "") {
            let data = res.data;
            this.company_id = data.company_id;
            // 表示有公司信息
            let info = {};
            Object.keys(this.form).forEach(key => {
              info[key] = data[key];
            });
            this.form = info;

            this.imageUrls = {
              license_img_url: qiniuUrl + data.license_img,
              id_card_front_img_url: qiniuUrl + data.id_card_front_img,
              id_card_reverse_img_url: qiniuUrl + data.id_card_reverse_img
            };
          }
          console.log(res);
        })
        .catch(res => {
          console.error(res);
        });
    },
    // 输入完银行卡
    cardNoInput() {
      // 显示 银行卡 对应的 银行
      bankApi.getBankByCardNo(this.form.bank_card, bankName => {
        this.form.bank_name = bankName;
      });
    },

    // 上传营业执照
    uploadLicense(res) {
      // this.qiniu_upload(res, "");
      this.imageUrls.license_img_url = URL.createObjectURL(res.file);
      if (res != this.imageRes.license_img_res) {
        this.imageRes.license_img_res = res;
        this.form.license_img = "";
      }
    },
    // 上传身份证
    uploadCare1(res) {
      this.imageUrls.id_card_front_img_url = URL.createObjectURL(res.file);
      if (res != this.imageRes.id_card_front_img_res) {
        this.imageRes.id_card_front_img_res = res;
        this.form.id_card_reverse_img = "";
      }
    },
    uploadCare2(res) {
      this.imageUrls.id_card_reverse_img_url = URL.createObjectURL(res.file);
      if (res != this.imageRes.id_card_reverse_img_res) {
        this.imageRes.id_card_reverse_img_res = res;
        this.form.id_card_reverse_img = "";
      }
    },

    qiniu_upload(req, imagekey) {
      return new Promise((resolve, reject) => {
        // 如果已经上传过，就不用上传
        if (imagekey) {
          return resolve(imagekey);
        }
        qiniuApi.upload(
          req,
          next => {
            // 上传进度 total.percent
          },
          error => {
            // console.log(error);
            reject(error);
          },
          complete => {
            // console.error(complete);
            resolve(complete.key);
            // let hash = complete.hash;
            // let key = complete.key;
            // console.log(hash);
            // console.log(key);
          }
        );
      });
    },

    beforeFileUpload(file) {
      const isJPG = file.type === "image/jpeg" || file.type === "image/png";
      const isLt2M = file.size / 1024 / 1024 <= 3;

      if (!isJPG) {
        this.$message.error("上传企业营业执照图片格式不正确!");
      }
      if (!isLt2M) {
        this.$message.error("上传企业营业执照大小不能超过3MB!");
      }
      return isJPG && isLt2M;
    },

    async uploadAllImage(cb) {
      try {
        await this.qiniu_upload(
          this.imageRes.license_img_res,
          this.form.license_img
        ).then(key => {
          this.form.license_img = key;
        });

        await this.qiniu_upload(
          this.imageRes.id_card_front_img_res,
          this.form.id_card_front_img
        ).then(key => {
          this.form.id_card_front_img = key;
        });

        await this.qiniu_upload(
          this.imageRes.id_card_reverse_img_res,
          this.form.id_card_reverse_img
        ).then(key => {
          this.form.id_card_reverse_img = key;
        });
      } catch (error) {
        console.log(" uploadAllImage 上传图片失败--- ", error);
      }

      cb();
    },

    // 提交数据
    requestUpdata() {
      this.$loading.show("正在上传数据中...");
      this.uploadAllImage(() => {
        if (!this.form.license_img) {
          this.$loading.hide();
          this.$message.error("企业营业执照上传失败！");
          return;
        }
        if (!this.form.id_card_front_img) {
          this.$loading.hide();
          this.$message.error("身份证人像面上传失败！");
          return;
        }
        if (!this.form.id_card_reverse_img) {
          this.$loading.hide();
          this.$message.error("身份证国徽面上传失败！");
          return;
        }

        // 修改带上 company_id
        if (this.company_id) {
          this.form.company_id = this.company_id;
        }

        companyApi
          .uploadCompanyInfo(this.form)
          .then(res => {
            // console.log("提交数据成功 ", res);
            this.$message({
              message: "提交成功",
              type: "success"
            });
            this.updateVerifyStatus("verifieding"); // 更新认证状态
            this.resetForm();
          })
          .catch(res => {
            if (res && res.data?.msg) {
              this.$message.error(res.data.msg);
            }
          })
          .then(res => {
            this.$loading.hide();
          });
      });
    },

    // 提交
    submitAction() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          // 如果是图片没有通过 可以使用 message 提示
          if (!this.imageUrls.license_img_url) {
            this.$message.error("请上传企业营业执照");
            return false;
          }
          if (!this.imageUrls.id_card_front_img_url) {
            this.$message.error("请上传身份证人面像");
            return false;
          }
          if (!this.imageUrls.id_card_reverse_img_url) {
            this.$message.error("请上传省份证国徽面");
            return false;
          }

          this.requestUpdata();
        } else {
          return false;
        }
      });
    },

    // 重置表格
    resetForm() {
      this.$refs.ruleForm.resetFields();
      Object.getOwnPropertyNames(this.imageUrls).forEach(key => {
        this.imageUrls[key] = "";
      });
    }
  },
  //引入的组件
  components: {}
};
</script>
