// import axios from "axios";
import network from '../network'
import banks from "utils/bank.json"

// 通过银行卡号，获取是哪家银行
function getBankByCardNo(cardNo, callback) {
    // 6222005865412565805
    let url = `https://ccdcapi.alipay.com/validateAndCacheCardInfo.json?cardNo=${cardNo}&cardBinCheck=true`

    network.get(url, {
        withCredentials: false
    }).then(res => {
        let bankName = "";
        if (res && res.bank) {
            let bank = res.bank;
            bankName = banks[bank];
        }
        callback(bankName)
    }).catch(res => {

    })


    // axios({
    //     url: url,
    //     method: "get"
    // }).then(res => {
    //     let bankName = "";
    //     if (res.data && res.data.bank) {
    //         let bank = res.data.bank;
    //         bankName = banks[bank];
    //     }
    //     callback(bankName)
    // }).catch(res => {
    // })
}

export default {
    getBankByCardNo
}